@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
body {
  font-family: 'Roboto', sans-serif;
  background-color: #1f2833;
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  text-align: center;
}


h1, h2, h3 {
  font-family: 'Raleway', sans-serif;
}

h1 {
  color: #AC3B61;
  font-weight: 800;
}

h3 {
  color: #45a29e;
}
.app-header {
  display: flex;
  justify-content: space-around;
  background-color: #0b0c10;
  padding: 2em 2em 1em;
  text-align: center;
}

.app-header__nav {
  min-width: 700px;
}

.app-header h1 {
  color: #f7f9fb;
  font-size: 3em;
  margin: 1em 0 1.5em;
}

.app-header ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  margin: 0;
  text-transform: uppercase;
}

.app-header ul a {
  font-size: 1.2em;
  font-weight: 800;
  color: #00adb5;
  letter-spacing: 0.1em;
  text-decoration: none;
}

.app-header ul a:hover {
  color: #222831;
}

.app-header__selections {
  max-height: 150px;
  box-shadow: 7px 7px;
  background-color: rgb(72, 72, 72);
  padding: 1em 2em 2em 1em;
}

.app-header__selections h2 {
  color: #00adb5;
}

.app-header__selections p {
  text-align: left;
  color:  white;
}


.input-search {
	height: 2em;
	width: 15em;
	margin-bottom: 1em;
}

.input-search-button {
	padding: 0.3em 0.5em;
}
.movie-listing {
	background-color: #f7f9fb;
	display: grid;
	grid-template-columns: auto 2fr;
	grid-gap: 1em;
	gap: 1em;
	width: 40em;
	margin: 1em;
	padding: 1em;
}


.show-all-movies {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}
.item-link {
	color: #45A29E;
	display: block;
	background-color: #cedceb;
	width: 40em;
	margin: 0 1em 2em;
	padding: 1em;
	text-decoration: none;
}

.item-link:hover {
	font-style: italic;
}

.add-movie-alert {
	background-color: #cedceb;
	margin-top: 2em;
	padding: 1em;
}
.customers {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}
.customer-box {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  padding: 10px;
  width: 22em;
	background-color: #f7f9fb;
}

